@import url(https://fonts.googleapis.com/css?family=Advent+Pro:100);

button {
  padding: 10px 15px;
  background-color: rgb(70, 153, 179);
  color: white;
  border: 1px solid rgb(70, 153, 179);
  background-color: 250ms;
  border-radius: 12px !important;
}

button:hover {
  cursor: pointer;
  background-color: white;
  color: rgb(70, 153, 179);
  border-radius: 12px;
}

.projects {
  text-align: center;
}
.contactForm {
  background-color: white;
  color: white;
  position: relative;
  top: 150px;
  display: block;
  height: 500px;
  background: rgba(75, 74, 74, 0.753);
  border-radius: 5px;
  overflow: hidden;
  z-index: 1;
  max-width: 750px !important;
}
form {
  max-width: 500px;
  width: 100%;
  margin: 0 auto;
}
.titles {
  padding-top: 25px;
  color: white;
  text-shadow: 1px 1px 2px black, 0 0 1em black, 0 0 0.2em black;
}
.skillItem {
  color: white;
}
li,
ul {
  list-style-type: none;
}
.card {
  padding: 20px;
}
a {
  cursor: pointer;
  color: white !important;
  padding: 10px;
}
a:hover {
  text-decoration: underline !important;
}

.topHead {
  background: url(https://media.giphy.com/media/xT9IgN8YKRhByRBzMI/giphy.gif)
    no-repeat center center fixed;
  background-size: cover;
  background-color: #272525;
}
.topText {
  text-align: center;
  position: absolute;
  top: 37%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
  text-shadow: 1px 1px 2px black, 0 0 1em black, 0 0 0.2em black;
  font: 5em Georgia, serif;
}
.topHead {
  width: 100%;
  height: 1000px;
}

body {
  background: url(/static/media/coding.0a390081.jpg) no-repeat center center fixed;
  background-size: cover;
  background-color: #272525;
}
.submitbtn {
  float: right;
}
.card {
  background-color: black;
}
.skills {
  background-size: cover;
  background-color: #272525;
  padding-bottom: 35px;
}
.about {
  background: url(/static/media/c130.2cabcfa1.jpg) no-repeat center center;
  background-size: cover;
  background-color: #272525;
  height: 800px;
  text-align: center;
}
.aboutText {
  padding: 50px;
}
.topButton {
  background-color: black;
  color: white;
  text-align: center;
}

.NavBar {
  width: 100%;
  color: white;
  height: 50px;
  position: fixed !important;
  overflow: hidden;
  z-index: 999999;
  text-align: center;
  background-color: #000000 !important;
}
.resume {
  float: right !important;
}

